@import "https://fonts.googleapis.com/css2?family=Cairo:wght@200..1000&family=Oswald:wght@200..700&display=swap";
:root {
  --spaceblue: #011b45;
  --darkerblue: #02132e;
  --ligtherblue: #042252;
  --violet: #5e2f64;
  --lighterviolet: #9c6ba8;
  --lighterlighterviolet: #ac7bb8;
  --accentpink: #ffa8e2;
  --silver: #d9d9d9;
  --moonglow: #f4f4f4;
}

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

#bottom-bar {
  z-index: 1000;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

#toggle-menu {
  background-color: var(--darkerblue);
  color: var(--moonglow);
  cursor: pointer;
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  font-size: 24px;
  transition: background-color .2s, transform .2s, box-shadow .2s;
  display: flex;
  box-shadow: 0 4px 8px #0003;
}

#toggle-menu:hover {
  background-color: var(--moonglow);
  color: var(--darkerblue);
  transform: scale(1.1);
  box-shadow: 0 6px 12px #0000004d;
}

#menu {
  background-color: #02132ecc;
  border-radius: 10px;
  flex-direction: column;
  gap: 10px;
  padding: 10px;
  transition: opacity .3s, transform .3s;
  display: flex;
  position: absolute;
  bottom: 70px;
  right: 0;
  box-shadow: 0 4px 8px #0003;
}

#menu.hidden {
  opacity: 0;
  pointer-events: none;
  transform: translateY(20px);
}

#menu button {
  background-color: var(--lighterviolet);
  color: var(--darkerblue);
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-family: Cairo, sans-serif;
  font-size: 1em;
  font-weight: bolder;
  transition: background-color .2s;
}

#menu button:hover {
  background-color: var(--lighterlighterviolet);
}

#loading-screen {
  z-index: 9999;
  background: #000;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.spinner {
  border: 8px solid #ffffff4d;
  border-top-color: #000;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

canvas {
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}
